<template>
  <div>
    <span :style="{'border-bottom': '3px solid' + borderColor,'color' : fontColor,'font-weight': fontWeight}">{{title}}</span>
  </div>
</template>
<script>
  export default {
    name: 'VTitle',
    props: {
      title: {
        type: String,
        default: ''
      },
      borderColor: {
        type: String,
        default: '#541f7e'
      },
      fontColor: {
        type: String,
        default: '#541f7e'
      },
      fontWeight: {
        type: String,
        default: 'bold'
      }
    }
  }
</script>
<style scoped lang="stylus">
  div
    border-bottom 1px solid #e2e2e2
    span
      display inline-block
      padding 0 4px
      padding-bottom 10px
      font-family: MicrosoftYaHei;
      font-size: 18px;
      text-align: left;
</style>


