<template>
  <div>
    <div v-for="(item, index) in pages" :key="index" class="page"
         :class="{'active': activeIndex === index}"
         @click="choose(index)">{{item}}</div>
  </div>
</template>
<script>
  export default {
    name: 'Pager',
    props: {
      pageNum: {
        type: Number,
        default: 4
      },
      nowIndex: {
        type: Number,
        default: 1
      }
    },
    data () {
      return {
        activeIndex: 0
      }
    },
    created () {
      this.activeIndex = this.nowIndex
    },
    computed: {
      pages () {
        let pages = []
        for (let i = 1; i <= this.pageNum; i++) {
          pages.push(i)
        }
        return pages
      }
    },
    methods: {
      choose (index) {
        this.activeIndex = index
      }
    }
  }
</script>
<style scoped lang="stylus">
.page
  border 1px solid #541f7e
  padding 12px 16px
  display inline-block
  font-size 14px
  margin-right 8px
  cursor pointer
.active
  background #541f7e
  color #fff
</style>


