<template>
  <div>
    <div class="question" v-for="(item, index) in question">
      <div class="qtitle">
        {{item.question}}
      </div>
      <div class="answer">
        <p>{{item.answer}}</p>
      </div>
    </div>
    <div class="question">
      <div class="qtitle" style="text-decoration:underline;cursor:pointer">
        {{$t('m.moreQuestion')}}>>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'Question',
    props: {
      question: {
        type: Array,
        default: []
      }
    }
  }
</script>
<style scoped lang="stylus">
.question
  line-height 1.29
  .qtitle
    font-family: MicrosoftYaHei;
    font-size: 14px;
    font-weight: bold;
    line-height: 1.71;
    text-align: left;
    color: #383838;
  .answer
    p
      text-indent 2em
      line-height: 1.71;
    div
      margin-left 2em
</style>


