<template>
  <div>
    <div class="nav">
      <span>{{firstTitle}} / </span>
      <span style="color: #555555;">{{title}}</span>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'VNav',
    props: {
      title: {
        type: String,
        default: '商务合作'
      },
      firstTitle: {
        type: String,
        default: '新闻中心'
      }
    }
  }
</script>
<style scoped lang="stylus">
@import '../common/stylus/mixin.styl'
.nav
  bg-image('../assets/news/icon-address')
  padding-left 22px
  background-position 2px
  span
    font-family: MicrosoftYaHei;
    font-size: 14px;
    line-height: 1.14;
    text-align: left;
    color: #999999;
</style>


