<template>
  <div class="slider">
    <div class="slider-img" @mouseover="clearInv" @mouseout="runInv">
      <transition name="slider">
        <img
          v-for="(item, index) in slides"
          :src="item.src"
          :key="index"
          v-if="nowIndex === index"
        />
      </transition>
      <div class="slider-icon">
        <ul>
          <li v-for="(item, index) in slides" @click="goto(index)" @mouseover="goto(index)">
            <a :class="{ active: index === nowIndex }"></a>
          </li>
        </ul>
      </div>
    </div>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'Slider',
  props: {
    slides: {
      type: Array,
      default: []
    },
    inv: {
      type: Number,
      default: 2000
    }
  },
  data () {
    return {
      nowIndex: 0
    }
  },
  watch: {
  },
  computed: {
    prevIndex () {
      if (this.nowIndex === 0) {
        return this.slides.length - 1
      } else {
        return this.nowIndex - 1
      }
    },
    nextIndex () {
      if (this.nowIndex === this.slides.length - 1) {
        return 0
      } else {
        return this.nowIndex + 1
      }
    }
  },
  methods: {
    goto (index) {
      this.nowIndex = index
    },
    runInv () {
      this.invId = setInterval(() => {
        this.goto(this.nextIndex)
      }, this.inv)
    },
    clearInv () {
      clearInterval(this.invId)
    }
  },
  mounted () {
    this.runInv()
  }
}
</script>

<style scoped lang='stylus'>
&.slider-enter
  transform translateX(100%)
&.slider-enter-active, .slider-leave-active
  transition all 1.2s
&.slider-leave-to
  transform translateX(-100%)
.slider
  width 100%
  height 438px
  position relative
  overflow hidden
  .slider-img
    position absolute
    top 0
    left 0
    width 100%
    height 100%
    img
      width 100%
      height 100%
      position absolute
      top 0
    .slider-icon
      width 100%
      position absolute
      bottom 20px
      left 0
      ul
        list-style none
        float left
        position relative
        left 50%
        li
          margin 0 10px
          float left
          position relative
          right 50%
          a
            display block
            width 8px
            height 8px
            border 1px solid #fff
            border-radius 50%
            background #fff
            vertical-align middle
            cursor pointer
          .active
            width 14px
            height 14px
            position relative
            top -3px
</style>



