<template>
  <div class="wrapper">
    <div class="container navbar">
      <img src="../assets/home/logo.png" alt="">
      <ul>
        <li><router-link :to="{path:'/'}">{{$t('m.home')}}</router-link></li>
        <li style="height:40px;position:relative"
            @click="listShows"
            @mouseenter="listShows"
            @mouseleave="listHide">
          <span>{{$t('m.about')}}Thai Fintech</span>
          <div class="introduce" v-if="listShow">
            <div v-for="(item, index) in introduce"
                 :class='{"active" : index === active2}' 
                 @mouseenter="listHover(index)" 
                 @mouseleave="listHoverOut(index)" 
                 ><router-link :to="{path: item.src}"><div class="itemdiv">{{item.title}}</div></router-link></div>
          </div>
        </li>
        <li><router-link :to="{path:'/news'}">{{$t('m.newCenter')}}</router-link></li>
        <li><router-link :to="{path:'/ThaiPayMall'}">ThaiPayMall</router-link></li>
        <li><router-link :to="{path:'/movepay'}">{{$t('m.mobilePayment1')}}</router-link></li>
      </ul>
      <div class="language" @click="languageShow"
           @mouseenter="languageShow"
           @mouseleave="languageHide">
        <span>{{useLanguage}}</span>
        <img src="../assets/home/icon-down-3.png" alt="">
        <div class="languagelist" v-if="show">
          <div v-for="(item, index) in language" :class='{"active" : index === active}' @mouseenter="hover(index)" @mouseleave="hoverOut(index)" @click="choose(item)">{{item}}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'VHeader',
    data () {
      return {
        show: false,
        listShow: false,
        active: 0,
        active2: 0,
        language: [
          '中文', 'ภาษาไทย', 'English'
        ],
        useLanguage: '中文',
        introduce: [
          {
            title: this.$t('m.companyIntroduce'),
            src: '/companyprofile'
          },
          {
            title: this.$t('m.businessCooperation'),
            src: '/cooperation'
          },
          {
            title: this.$t('m.joinUs'),
            src: '/joinus'
          },
          {
            title: this.$t('m.contactUs'),
            src: '/contactus'
          }
        ]
      }
    },
    created () {
      this.init()
    },
    methods: {
      listHide () {
        this.listShow = false
      },
      listHover (index) {
        this.active2 = index
      },
      listHoverOut () {
        this.active2 = -1
      },
      listShows () {
        this.listShow = true
      },
      languageShow () {
        this.show = true
      },
      languageHide () {
        this.show = false
      },
      hover (index) {
        this.active = index
      },
      hoverOut (index) {
        this.active = -1
      },
      choose (item) {
        this.useLanguage = item
        if (item === '中文') {
          this.$i18n.locale = 'cn'
          window.localStorage.setItem('thaiPayMallLanguage', 'cn')
          this.$router.go(0)
        } else if (item === 'ภาษาไทย') {
          this.$i18n.locale = 'thai'
          window.localStorage.setItem('thaiPayMallLanguage', 'thai')
          this.$router.go(0)
        } else {
          this.$i18n.locale = 'en'
          window.localStorage.setItem('thaiPayMallLanguage', 'en')
          this.$router.go(0)
        }
      },
      init () {
        let t = window.localStorage.getItem('thaiPayMallLanguage')
        if (t) {
          if (t === 'cn') {
            this.useLanguage = '中文'
          } else if (t === 'en') {
            this.useLanguage = 'English'
          } else {
            this.useLanguage = 'ภาษาไทย'
          }
        } else {
          this.useLanguage = '中文'
        }
      }
    }
  }
</script>
<style scoped lang="stylus">
  @import "../common/stylus/mixin"
  .wrapper
    width 100%
    border-top 2px solid #5c2985
    .navbar
      height 78px
      img
        margin-top 14px
      ul
        margin-left 64px
        list-style none
        display inline-block
        margin-top 40px
        vertical-align top
        li
          display inline-block
          margin-left 70px
          cursor pointer
          .introduce
            z-index 100
            padding-top 12px
            position absolute
            color #fff
            text-align center
            top 30px
            left -16px
            width 154px
            height 158px
            font-size 12px
            background url('../assets/home/icon-down.png') no-repeat
            div
              .itemdiv
                padding 10px 0
            .active
              background rgba(255, 255, 255, 0.2)
      .language
        width 100px
        height 40px
        posi
        display inline-block
        float right
        font-size 14px
        background url('../assets/home/icon.png') no-repeat left 2px
        padding-left 26px
        margin-top 38px
        cursor pointer
        span
          vertical-align top
        img
          vertical-align top
          margin-top 6px
          margin-left 10px
        .languagelist
          z-index 100
          color #fff
          text-align center
          position relative
          top 13px
          left -56px
          width 154px
          height 126px
          padding-top 22px
          background url('../assets/home/icon-down.png') no-repeat
          div
            padding 10px 0
          .active
            background rgba(255, 255, 255, 0.2)

</style>


