import Vue from 'vue'
import Router from 'vue-router'
// import Index from '@/components/index/index'
// import Mall from '@/components/mall/mall'
// import JoinUs from '@/components/joinus/joinus'
// import ContactUs from '@/components/contactus/contactus'
// import Cooperation from '@/components/cooperation/cooperation'
// import News from '@/components/news/news'
// import Media from '@/components/media/media'
// import Publish from '@/components/publish/publish'
// import Article from '@/components/article/article'
// import Profile from '@/components/profile/profile'
// import Express from '@/components/express/express'
// import MovePay from '@/components/movepay/movepay'
// import Partner from '@/components/partner/partner'
const Index = () => import('@/components/index/index')
const Mall = () => import('@/components/mall/mall')
const JoinUs = () => import('@/components/joinus/joinus')
const ContactUs = () => import('@/components/contactus/contactus')
const Cooperation = () => import('@/components/cooperation/cooperation')
const News = () => import('@/components/news/news')
const Media = () => import('@/components/media/media')
const Publish = () => import('@/components/publish/publish')
const Article = () => import('@/components/article/article')
const Profile = () => import('@/components/profile/profile')
const Express = () => import('@/components/express/express')
const MovePay = () => import('@/components/movepay/movepay')
const Partner = () => import('@/components/partner/partner')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/ThaiPayMall',
      name: 'Mall',
      component: Mall
    },
    {
      path: '/joinus',
      name: 'JoinUs',
      component: JoinUs
    },
    {
      path: '/contactus',
      name: 'ContactUs',
      component: ContactUs
    },
    {
      path: '/cooperation',
      name: 'Cooperation',
      component: Cooperation
    },
    {
      path: '/news',
      name: 'News',
      component: News
    },
    {
      path: '/media',
      name: 'Media',
      component: Media
    },
    {
      path: '/publish',
      name: 'Publish',
      component: Publish
    },
    {
      path: '/article',
      name: 'Article',
      component: Article
    },
    {
      path: '/companyprofile',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/express',
      name: 'Express',
      component: Express
    },
    {
      path: '/movepay',
      name: 'MovePay',
      component: MovePay
    },
    {
      path: '/partner',
      name: 'Partner',
      component: Partner
    }
  ]
})



